<template>
  <div class="update-name">
      <!--  -->
      <van-nav-bar
        title=""
        left-text=""
        right-text=""
        @click-left="$emit('close')"
        @click-right="onSubmit"
      />
      <!--  -->
      <div class="field-wrap">
          <van-field
            v-model.trim="localName"
            row="2"
            autosize
            type="textarea"
            maxlength="7"
            placeholder=""
            show-word-limit
          />
      </div>
  </div>
</template>
<script>
import { updataUserMessage } from '@/api/users'
export default {
  name: 'UpdateName',
  props: {
    value: {
      type: String,
      required: true
    }
  },
  data () {
    // console.log(this.value) 
    return {
      localName: this.value
    }
  },
  created () {},
  methods: {
    // 
    async onSubmit () {
      this.$toast.loading({
        message: '',
        duration: 0
      })
      try {
        const { data: res } = await updataUserMessage({
          name: this.localName
        })
        // console.log(res)
        this.$toast.success('')
        this.$emit('input', res.data.name)
        // 
        this.$emit('close')
      } catch (error) {
        this.$toast.fail('')
      }
    }
  },
  components: {}
}
</script>
<style scoped lang="scss">
.field-wrap{
    border:1px solid #aaa;
    margin:30px;
    /deep/ .van-field__control{
        padding: 14px;
    }
}
</style>
