<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar
      class="page-nav-bar"
      title=""
      left-arrow
      @click-left="$router.back()"
    />
    <!-- / -->

    <input type="file" @change="changeFile" hidden ref="userimg">

    <!--  -->
    <van-cell class="avatar-cell"  @click="chooseImg" title="" is-link center>
      <van-image
        class="avatar"
        round
        fit="cover"
        :src="userInfo.photo"
      />
    </van-cell>
    <van-cell title="" @click="isShowEditName=true" :value="userInfo.name" is-link />
    <van-cell title="" @click="isShowEditGender=true" :value="userInfo.gender===0? '':''" is-link />
    <van-cell title="" @click="isShowEditBirthday=true" :value="userInfo.birthday" is-link />
    <!-- / -->

    <!--  -->
    <van-popup v-model="isShowEditAvatar" position="bottom" :style="{height:'100%'}">
      <update-photo
      v-if="isShowEditAvatar"
       :imgurl="previewImgURL"
      @close="isShowEditAvatar=false"
      @addUserPhoto = "addUserPhoto"
      ></update-photo>
    </van-popup>

    <!--  -->
    <van-popup
    v-model="isShowEditName"
    position="bottom"
    :style="{height:'100%'}">
    <update-name
      v-if="isShowEditName"
      @close="isShowEditName=false"
      v-model="userInfo.name"
    ></update-name>
    </van-popup>

    <!--  -->
    <van-popup
    v-model="isShowEditGender"
    position="bottom"
    :style="{height:'40%'}">
    <update-gender
      v-if="isShowEditGender"
      @close="isShowEditGender=false"
      v-model="userInfo.gender"
    ></update-gender>
    </van-popup>

    <!--  -->
    <van-popup
    v-model="isShowEditBirthday"
    position="bottom"
    :style="{height:'50%'}">
    <update-birthday
      v-if="isShowEditBirthday"
      @close="isShowEditBirthday=false"
      v-model="userInfo.birthday"
    ></update-birthday>
    </van-popup>
  </div>
</template>

<script>
import { getUserInfoDetail } from '@/api/users'
import UpdatePhoto from './components/update-photo'
import UpdateName from './components/update-name'
import UpdateGender from './components/update-gender'
import UpdateBirthday from './components/update-birthday'

export default {
  name: 'UserProfile',
  components: {
    UpdatePhoto,
    UpdateName,
    UpdateGender,
    UpdateBirthday
  },
  props: {},
  data () {
    return {
      userInfo: [], // 
      isShowEditAvatar: false, // 
      previewImgURL: '',
      isShowEditName: false, // 
      isShowEditGender: false, // 
      isShowEditBirthday: false // 
    }
  },
  computed: {},
  watch: {},
  created () {
    // 
    this.loadingGetUserInfoDetail()
  },
  mounted () {},
  methods: {
    // 
    async loadingGetUserInfoDetail () {
      try {
        const { data: res } = await getUserInfoDetail()
        // console.log(res)
        // 
        this.userInfo = res.data
      } catch (error) {
        this.$toast.fail('')
      }
    },
    // 
    chooseImg () {
      // input
      this.$refs.userimg.click()
    },
    // 
    changeFile () {
      // console.log(this.$refs.userimg.files[0]) // 
      // console.log(URL.createObjectURL(this.$refs.userimg.files[0])) // 
      const fileImgs = this.$refs.userimg.files
      if (fileImgs.length) {
        // 
        this.previewImgURL = URL.createObjectURL(fileImgs[0])
        // 
        this.isShowEditAvatar = true
      }
    },
    // 
    addUserPhoto (val) {
      this.userInfo.photo = val
      // 
      this.isShowEditAvatar = false
    }
  }
}
</script>

<style scoped lang="scss">
.user-profile {
  .avatar-cell {
    .van-cell__value {
      display: flex;
      flex-direction: row-reverse;
    }
    .avatar {
      width: 60px;
      height: 60px;
    }
  }
}
</style>
