<template>
  <div class="update-photo">
    <img :src="imgurl" ref="img" alt="" />
    <div class="toolabr">
      <div class="cancel" @click="$emit('close')"></div>
      <div class="confirm" @click="updatePhoto"></div>
    </div>
  </div>
</template>

<script>
// JS
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

import { updataUserPhoto } from '@/api/users'
export default {
  name: 'UpdatePhoto',
  props: {
    // 
    imgurl: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      cropper: null // 
    }
  },
  mounted () {
    // 
    const image = this.$refs.img
    this.cropper = new Cropper(image, {
      viewMode: 1, // 
      dragMode: 'move', // move,crop
      aspectRatio: 1, //
      autoCropArea: 1,
      cropBoxMovable: false,
      cropBoxResizable: false,
      background: false,
      movable: true
    })
    // console.log(cropper)
  },
  methods: {
    // 
    updatePhoto () {
      this.$toast.loading({
        message: '',
        duration: 0
      })
      // 
      this.cropper.getCroppedCanvas().toBlob(async blob => {
        // blob
      // 
        try {
          const fd = new FormData()
          fd.append('photo', blob)
          const { data: res } = await updataUserPhoto(fd)
          // console.log(res)
          this.$toast.success('')
          this.$emit('addUserPhoto', res.data.photo)
        } catch (error) {
          this.$toast.fail('')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.update-photo {
  background-color: #000;
  height: 100%;
  img {
    max-width: 100%;
  }
  .toolabr {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: space-between;
  }
  .cancel,
  .confirm {
    width: 120px;
    height: 90px;
    font-size: 0.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
  }
}
</style>
